<template>
  <ChartDoughnut
    class="chart-doughnut"
    :options="optionWhoDelivery"
    :chart-data="datacollection"
    :key="data"
  />
</template>

<script>
import ChartDoughnut from "./ChartDoughnut";
import * as chartConfig from "./ChartConfig";
export default {
  components: {
    ChartDoughnut
  },
  computed: {
    currentState: {
      get() {
        return this.$store.state.currentState;
      },
      set(item) {
        this.$store.commit("setCurrentState", item);
      }
    }
  },
  data() {
    return {
      optionWhoDelivery: chartConfig.whoDelivery,
      datacollection: {}
    };
  },
  methods: {
    handleData(item, month, uf) {
      var client = this.$store.state.client;
      var stateDelivery = this.$store.state.dataReceive[client].whoDelivery.map(
        item => item[0]
      );
      function perUF(obj) {
        if (uf != "Geral") {
          if (stateDelivery[uf % 25] == obj[0]) {
            return true;
          } else {
            return false;
          }
        } else {
          return true;
        }
      }

      var valueTotal = this.$store.state.dataReceive[client][
        `whoDelivery${month == 0 ? "" : "_" + month.toString()}`
      ]
        .filter(perUF)
        .map(Element => Element[item])
        .map(Element => parseFloat(Element))
        .reduce((soma, atual) => soma + atual)
        .toString();

      return {
        valueTotal: valueTotal
      };
    },
    fillData() {
      this.datacollection = {
        datasets: [
          {
            data: [
              this.handleData(1, 0, this.currentState).valueTotal,
              this.handleData(2, 0, this.currentState).valueTotal,
              this.handleData(3, 0, this.currentState).valueTotal
            ],
            backgroundColor: [
              chartConfig.fontColor.verde,
              chartConfig.fontColor.laranja_claro,
              chartConfig.fontColor.rozado
            ],
            borderWidth: 2
          }
        ],

        // These labels appear in the legend and in the tooltips when hovering different arcs
        labels: ["Flash", "Terceiro", "Parceiro"]
      };
    }
  },
  mounted() {
    this.fillData();
    setInterval(() => {
      this.fillData();
    }, 2000);
  }
};
</script >

