<template>
  <div class="row mx-3 main-row">
    <div class="col-lg-3 col-md-6 col-12">
      <SummarySLA />
    </div>
    <div class="col-lg-3 col-md-6 col-12">
      <CardSummary
        :typeCard="'postado'"
        :greater="summaryPost.greater"
        :value="summaryPost.valueTotal"
        :textDownValue="summaryPost.percentage +'% total postado'"
      >
        <h5 slot="footer">
          <SumaryChartPost />
        </h5>
      </CardSummary>
    </div>
    <div class="col-lg-3 col-md-6 col-12">
      <CardSummary
        :typeCard="'recebido'"
        :greater="true"
        :value="summaryReceive.valueTotal"
        :textDownValue="((parseInt(summaryReceive.valueTotal)/parseInt(summaryPost.valueTotal)).toFixed(2)*100) +'% total postado'"
      >
        <h5 slot="footer">
          <SumaryChartReceive />
        </h5>
      </CardSummary>
    </div>

    <div class="col-lg-3 col-md-6 col-12">
      <CardSummary
        :typeCard="'devolvido'"
        :greater="false"
        :value="summaryReturned.valueTotal"
        :textDownValue="((parseInt(summaryReturned.valueTotal)/parseInt(summaryPost.valueTotal)).toFixed(2)*100) +'% total postado'"
      >
        <h5 slot="footer">
          <SumaryChartReturned />
        </h5>
      </CardSummary>
    </div>

    <div class="col-lg-8 col-md-8 col-12">
      <RowMain v-if="!table" />
      <Table v-if="table" />
    </div>
    <div class="col-lg-4 col-md-4 col-12">
      <RightCard />
    </div>
    <div class="col-lg-6 col-md-6 col-12">
      <CardTempoEntrega />
    </div>
    <div class="col-lg-6 col-md-6 col-12">
      <CardLogTail />
    </div>
  </div>
</template>

<script>
import SumaryChartReceive from "./../chart/SumaryChartReceive";
import SumaryChartPost from "./../chart/SumaryChartPost";
//import SumaryChartPending from "./../chart/SumaryChartPending";
import SumaryChartReturned from "./../chart/SumaryChartReturned";
//import CardInstagram from "./../cards/cardInstagram"
import CardLogTail from "./../cards/cardLogTail";
import SummarySLA from "./../cards/cardSummarySLA";
import CardTempoEntrega from "./../cards/cardTempoEntrega";
import CardSummary from "./../cards/cardSummary";
import RightCard from "./../cards/cardRightMain";
import RowMain from "./../cards/cardMainChart";
import Table from "./../cards/cardTableSla";
export default {
  components: {
    //  CardInstagram,
    RowMain,
    Table,
    RightCard,
    CardSummary,
    CardTempoEntrega,
    CardLogTail,
    SumaryChartPost,
    SumaryChartReturned,
    SummarySLA,
    // SumaryChartPending,
    SumaryChartReceive
  },
  computed: {
    summaryPost() {
      return this.handleData(5, 0, "Geral");
    },
    summaryReceive() {
      return this.handleData(1, 0, "Geral");
    },
    summaryReturned() {
      return this.handleData(6, 0, "Geral");
    },
    summaryPending() {
      return this.handleData(4, 0, "Geral");
    },
    table: {
      get() {
        return this.$store.state.openTable;
      },
      set(item) {
        this.$store.commit("setOpenTable", item);
      }
    }
  },
  methods: {
    handleData(item, month, uf) {
      var client = this.$store.state.client;
      var stateDelivery = this.$store.state.dataReceive[client].mainData.map(
        item => item[0]
      );
      console.log(stateDelivery);
      function perUF(obj) {
        if (uf != "Geral") {
          if (stateDelivery[uf] == obj[0]) {
            return true;
          } else {
            return false;
          }
        } else {
          return true;
        }
      }

      var valueTotal = this.$store.state.dataReceive[client][
        `mainData${month == 0 ? "" : "_" + month.toString()}`
      ]
        .filter(perUF)
        .map(Element => Element[item])
        .map(Element => parseInt(Element))
        .reduce((soma, atual) => soma + atual)
        .toString();
      var valueTotal_1 = this.$store.state.dataReceive[client][
        `mainData${"_" + (month + 1).toString()}`
      ]
        .filter(perUF)
        .map(Element => Element[item])
        .map(Element => parseInt(Element))
        .reduce((soma, atual) => soma + atual)
        .toString();

      return {
        greater: valueTotal > valueTotal_1,
        valueTotal: valueTotal,
        percentage: Math.abs((valueTotal / valueTotal_1) * 100 - 100)
          .toFixed(0)
          .toString()
      };
    }
  }
};
</script>

<style lang="sass">



@keyframes slide-in
  to  
    transform: translateY(0)
  from
    transform: translateY(100px)

@keyframes slide-out
  to
    transform: translateY(0)
  from
    transform: translateY(20px)








</style>