<template>
  <ChartBar class="chart-log-tail" :options="optionsChartBar" :chart-data="datacollection" />
</template>

<script>
import ChartBar from "./ChartBar";
//import * as chartConfig from "./ChartConfig";
import { fontColor } from "./ChartConfig";
export default {
  components: {
    ChartBar
  },
  data() {
    return {
      datacollection: null,
      optionsChartBar: {
        /*OPTIONS Start*/

        responsive: true,
        maintainAspectRatio: false,
        /*layout:{
        padding:{
            left: 50
        }
    }*/
        legend: {
          display: false,
          labels: {
            fontColor: fontColor.branco,
            fontFamily: fontColor.font_principal,
            fontSize: 13
          }
        },
        scales: {
          yAxes: [
            {
              id: "left-y-axis",
              gridLines: {
                zeroLineWidth: 0,
                zeroLineColor: "rgb(230, 183, 51)",
                display: false,
                color: fontColor.azul_maisclaro,
                drawOnChartArea: false
              },
              ticks: {
                display: false,
                fontColor: fontColor.branco,
                fontFamily: fontColor.font_principal,
                fontStyle: "italic",
                fontSize: 15,
                padding: 30,
                callback: function(x) {
                  var arrayX = x.toString().split("");
                  arrayX.reverse();
                  let countLen = arrayX.length - 1;
                  if (Math.floor(countLen / 3) > 0) {
                    for (let i = 0; i < Math.floor(countLen / 3); i++) {
                      arrayX.splice(3 * (i + 1) + i, 0, ".");
                    }
                  }
                  arrayX.reverse();
                  return arrayX.join("");
                },

                min: 0,
                maxTicksLimit: 5
              }
            },
            {
              id: "right-y-axis",
              position: "right",
              gridLines: {
                zeroLineWidth: 0,
                zeroLineColor: "rgb(230, 183, 51)",
                display: false,
                color: fontColor.azul_maisclaro,
                drawOnChartArea: false
              },
              ticks: {
                display: false,
                fontColor: fontColor.branco,
                fontFamily: fontColor.font_principal,
                fontStyle: "italic",
                fontSize: 15,
                padding: 30,
                callback: function(x) {
                  var arrayX = x.toString().split("");
                  arrayX.reverse();
                  let countLen = arrayX.length - 1;
                  if (Math.floor(countLen / 3) > 0) {
                    for (let i = 0; i < Math.floor(countLen / 3); i++) {
                      arrayX.splice(3 * (i + 1) + i, 0, ".");
                    }
                  }
                  arrayX.reverse();
                  return arrayX.join("");
                },

                min: 0,
                maxTicksLimit: 5
              }
            }
          ],
          xAxes: [
            {
              gridLines: {
                zeroLineWidth: 0,
                display: false,
                drawBorder: false,
                drawOnChartArea: false,
                drawTicks: false
              },
              ticks: {
                display: false,
                fontColor: fontColor.branco,
                fontFamily: fontColor.font_principal,
                fontWidth: 200,
                fontStyle: "italic",
                beginAtZero: true,
                padding: 15
              }
            }
          ]
        }

        /*OPTIONS End*/
      }
    };
  },
  methods: {
    fillData() {
      this.datacollection = {
        /*Data Start*/

        labels: ["Mar", "Abr", "Mai"],
        datasets: [
          {
            yAxisID: "left-y-axis",
            label: "Log periodo",
            backgroundColor: fontColor.azul_grafico,
            data: [12, 14, 16]
          },
          {
            yAxisID: "right-y-axis",
            label: "Log Total",
            backgroundColor: fontColor.verde,
            data: [16, 14, 12]
          }
        ]

        /*Data End*/
      };
    }
  },
  mounted() {
    this.fillData();
  }
};
</script>

