<template>
  <ChartLine class="time-delivery-third-chart" :chart-data="data" :options="optionsTimeDelivery" />
</template>

<script>
import ChartLine from "./ChartLine";
import { fontColor } from "./ChartConfig";
import * as chartConfig from "./ChartConfig";
export default {
  components: {
    ChartLine
  },
  data() {
    return {
      data: {},
      ///////////////////////////////////////// Start config ////////////////////
      optionsTimeDelivery: {
        responsive: true,
        maintainAspectRatio: false,
        /*layout:{
        padding:{
            left: 50
        }
    }*/
        legend: {
          display: false,
          labels: {
            fontColor: fontColor.azul_maisclaro,
            fontFamily: fontColor.font_principal,
            fontSize: 13
          }
        },
        tooltips: {
          enabled: false
        },
        animation: {
          duration: 1,
          onComplete: function() {
            if (window.innerWidth > 480) {
              var chartInstance = this.chart,
                ctx = chartInstance.ctx;
              ctx.font = " italic 10px " + fontColor.font_principal;
              ctx.textAlign = "center";
              ctx.textBaseline = "bottom";
              ctx.fillStyle = fontColor.branco;
              this.data.datasets.forEach(function(dataset, i) {
                var meta = chartInstance.controller.getDatasetMeta(i);
                meta.data.forEach(function(bar, index) {
                  var data = dataset.data[index];
                  ctx.fillText(data + " dias", bar._model.x, bar._model.y - 5);
                });
              });
            }
          }
        },
        scales: {
          yAxes: [
            {
              id: "rigth-y-axis",
              position: "right",
              gridLines: {
                zeroLineWidth: 0,
                zeroLineColor: "rgb(230, 183, 51)",
                display: false,
                color: fontColor.azul_maisclaro,
                drawOnChartArea: false
              },
              ticks: {
                display: true,
                fontColor: fontColor.transparente,
                fontFamily: fontColor.font_principal,
                fontStyle: "italic",
                fontSize: 15,
                padding: 1,
                callback: function(x) {
                  var arrayX = x.toString().split("");
                  arrayX.reverse();
                  let countLen = arrayX.length - 1;
                  if (Math.floor(countLen / 3) > 0) {
                    for (let i = 0; i < Math.floor(countLen / 3); i++) {
                      arrayX.splice(3 * (i + 1) + i, 0, ".");
                    }
                  }
                  arrayX.reverse();
                  return arrayX.join("");
                },

                min: 0,
                maxTicksLimit: 5
              }
            },
            {
              id: "left-y-axis",
              position: "left",
              gridLines: {
                zeroLineWidth: 0,
                zeroLineColor: "rgb(230, 183, 51)",
                display: false,
                color: fontColor.azul_maisclaro,
                drawOnChartArea: false
              },
              ticks: {
                display: true,
                fontColor: fontColor.transparente,
                fontFamily: fontColor.font_principal,
                fontStyle: "italic",
                fontSize: 15,
                padding: 1,
                callback: function(x) {
                  var arrayX = x.toString().split("");
                  arrayX.reverse();
                  let countLen = arrayX.length - 1;
                  if (Math.floor(countLen / 3) > 0) {
                    for (let i = 0; i < Math.floor(countLen / 3); i++) {
                      arrayX.splice(3 * (i + 1) + i, 0, ".");
                    }
                  }
                  arrayX.reverse();
                  return arrayX.join("");
                },
                max: 20,
                min: 0
              }
            },
            {
              id: "rigth-y-axis",
              position: "right",
              gridLines: {
                zeroLineWidth: 0,
                zeroLineColor: "rgb(230, 183, 51)",
                display: false,
                color: fontColor.azul_maisclaro,
                drawOnChartArea: false
              },
              ticks: {
                display: false,
                fontColor: fontColor.branco,
                fontFamily: fontColor.font_principal,
                fontStyle: "italic",
                fontSize: 15,
                padding: 30,
                callback: function(x) {
                  var arrayX = x.toString().split("");
                  arrayX.reverse();
                  let countLen = arrayX.length - 1;
                  if (Math.floor(countLen / 3) > 0) {
                    for (let i = 0; i < Math.floor(countLen / 3); i++) {
                      arrayX.splice(3 * (i + 1) + i, 0, ".");
                    }
                  }
                  arrayX.reverse();
                  return arrayX.join("");
                },

                min: 0,
                maxTicksLimit: 5
              }
            },
            {
              id: "right-y-axis",
              position: "right",
              gridLines: {
                zeroLineWidth: 0,
                zeroLineColor: "rgb(230, 183, 51)",
                display: false,
                color: fontColor.azul_maisclaro,
                drawOnChartArea: false
              },
              ticks: {
                display: true,
                fontColor: fontColor.transparente,
                fontFamily: fontColor.font_principal,
                fontStyle: "italic",
                fontSize: 15,
                padding: 30,
                callback: function(x) {
                  var arrayX = x.toString().split("");
                  arrayX.reverse();
                  let countLen = arrayX.length - 1;
                  if (Math.floor(countLen / 3) > 0) {
                    for (let i = 0; i < Math.floor(countLen / 3); i++) {
                      arrayX.splice(3 * (i + 1) + i, 0, ".");
                    }
                  }
                  arrayX.reverse();
                  return arrayX.join("");
                },
                max: 20,
                min: 0
              }
            }
          ],
          xAxes: [
            {
              gridLines: {
                zeroLineWidth: 0,
                display: false,
                drawBorder: false,
                drawOnChartArea: false,
                drawTicks: false
              },
              ticks: {
                display: false,
                fontColor: fontColor.branco,
                fontFamily: fontColor.font_principal,
                fontWidth: 200,
                fontStyle: "italic",
                beginAtZero: true,
                padding: 15
              }
            }
          ]
        }
      }

      ///////////////////////////////////// End config ////////////////////////////////////////
    };
  },
  methods: {
    fillData() {
      this.data = {
        labels: ["Mar", "Abr", "Mai"],
        datasets: [
          /*{
            label: "Postado",
            data: [100, 200, 150],
            borderWidth: 2.5,
            borderColor: chartConfig.fontColor.azul_superclaro,
            //"rgb(230, 183, 51)",
            backgroundColor: chartConfig.fontColor.azul_superclaro_opacity,
            fontColor: "rgb(230, 183, 51)",
            yAxisID: "left-y-axis",
            borderDash: [3, 2],
            pointHitRadius: 5,
            pointBackgroundColor: "rgba(0,0,0,0)"
          },*/
          {
            yAxisID: "left-y-axis",
            label: "SLA",
            data: [7.2, 2, 12],
            borderWidth: 2.5,
            borderColor: chartConfig.fontColor.azul_grafico,
            backgroundColor: chartConfig.fontColor.azul_maisclaro_opacity,
            fontColor: "rgb(230, 183, 51)",
            pointHitRadius: 5,
            pointRadius: 5,
            pointBackgroundColor: chartConfig.fontColor.branco,
            pointBorderColor: chartConfig.fontColor.rozado
          }
        ]
      };
    }
  },
  mounted() {
    this.fillData();
  }
};
</script>

