<template>
  <ChartDoughnut
    class="sla-chart"
    :options="optionWhoDelivery"
    :chart-data="datacollection"
    :key="data"
  />
</template>

<script>
import ChartDoughnut from "./ChartDoughnut";
import * as chartConfig from "./ChartConfig";
export default {
  components: {
    ChartDoughnut
  },
  data() {
    return {
      optionWhoDelivery: chartConfig.RTChart,
      datacollection: null
    };
  },
  methods: {
    fillData() {
      this.datacollection = {
        datasets: [
          {
            data: [98, 2],
            backgroundColor: [
              chartConfig.fontColor.verde,
              chartConfig.fontColor.rozado
            ],
            borderWidth: 2
          }
        ],

        // These labels appear in the legend and in the tooltips when hovering different arcs
        labels: ["Red", "Blue"]
      };
    }
  },
  mounted() {
    this.fillData();
  }
};
</script >

