<template>
  <div class="login container-fluid pt-5" style="height:100vh;width:100%">
    <div class="card mx-auto my-0 p-4">
      <div v-if="wrongPassword" class="alert alert-danger">Crendenciais incorretas</div>

      <img
        src="https://dashboardflash.s3-eu-west-1.amazonaws.com/login.png"
        style="height:86px; width:145px"
        class="mx-auto"
        alt
      />
      <div class="form-group">
        <label for="inputUsername">Usuário</label>
        <input
          type="text"
          class="form-control"
          id="input_username"
          aria-describedby="usernameHelp"
          placeholder="Entre com usuário"
          v-model="username"
        />
      </div>
      <div class="form-group">
        <label for="inputPassword">Senha</label>
        <input
          type="password"
          class="form-control"
          id="inputPassword"
          placeholder="Senha"
          v-model="password"
        />
      </div>
      <button href="/dashboard" class="btn btn-primary" @click.prevent="goDashboard">Entrar</button>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    currentClient() {
      return this.$store.state.currentClient;
    }
  },
  data() {
    return {
      wrongPassword: false,
      notLogin: null,
      username: "",
      password: ""
    };
  },
  methods: {
    goDashboard() {
      this.$store.state.users.forEach(value => {
        if (!this.notLogin) {
          if (
            value.username == this.username &&
            value.password == this.password
          ) {
            this.notLogin = true;
            this.$store.dispatch("increment");
            this.$store.commit("setClient", this.username);
          }
        }
      });
      if (this.notLogin) {
        console.log(this.username, this.password);
        this.$store.commit("setCurrentClient", this.username);
        this.$router.push("/dashboard");
      } else {
        this.wrongPassword = true;
      }
    }
  }
};
</script>

