<template>
  <ChartBar class="total-post" :options="optionTotalPost" :chart-data="datacollect" />
</template>
<script>
import ChartBar from "./ChartBar";
import * as chartConfig from "./ChartConfig";
import { fontColor } from "./ChartConfig";
export default {
  components: { ChartBar },
  data() {
    return {
      optionTotalPost: {
        /*Start options*/

        responsive: true,
        maintainAspectRatio: false,
        /*layout:{
        padding:{
            left: 50
        }
    }*/

        tooltips: {
          enabled: false
        },
        animation: {
          duration: 1,
          onComplete: function() {
            var chartInstance = this.chart,
              ctx = chartInstance.ctx;
            ctx.font = " italic 15px " + fontColor.font_principal;
            ctx.textAlign = "center";
            ctx.textBaseline = "bottom";
            ctx.fillStyle = fontColor.branco;
            this.data.datasets.forEach(function(dataset, i) {
              var meta = chartInstance.controller.getDatasetMeta(i);
              meta.data.forEach(function(bar, index) {
                var data = dataset.data[index];
                ctx.fillText(data + " %", bar._model.x, bar._model.y - 5);
              });
            });
          }
        },
        legend: {
          display: false,
          labels: {
            fontColor: fontColor.branco,
            fontFamily: fontColor.font_principal,
            fontSize: 13
          }
        },
        scales: {
          yAxes: [
            {
              // id: 'left-y-axis',
              gridLines: {
                zeroLineWidth: 0,
                zeroLineColor: "rgb(230, 183, 51)",
                display: false,
                color: fontColor.azul_maisclaro,
                drawOnChartArea: true
              },
              ticks: {
                display: false,
                fontColor: fontColor.azul,
                fontFamily: fontColor.font_principal,
                fontStyle: "italic",
                fontSize: 15,
                padding: 30,
                callback: function(x) {
                  var arrayX = x.toString().split("");
                  arrayX.reverse();
                  let countLen = arrayX.length - 1;
                  if (Math.floor(countLen / 3) > 0) {
                    for (let i = 0; i < Math.floor(countLen / 3); i++) {
                      arrayX.splice(3 * (i + 1) + i, 0, ".");
                    }
                  }
                  arrayX.reverse();
                  return arrayX.join("");
                },
                max: 100,
                min: 0,
                maxTicksLimit: 5
              }
            },
            {
              id: "right-y-axis",
              position: "right",
              gridLines: {
                zeroLineWidth: 0,
                zeroLineColor: "rgb(230, 183, 51)",
                display: false,
                color: fontColor.azul_maisclaro,
                drawOnChartArea: false
              },
              ticks: {
                display: false,
                fontColor: fontColor.branco,
                fontFamily: fontColor.font_principal,
                fontStyle: "italic",
                fontSize: 15,
                padding: 30,
                callback: function(x) {
                  var arrayX = x.toString().split("");
                  arrayX.reverse();
                  let countLen = arrayX.length - 1;
                  if (Math.floor(countLen / 3) > 0) {
                    for (let i = 0; i < Math.floor(countLen / 3); i++) {
                      arrayX.splice(3 * (i + 1) + i, 0, ".");
                    }
                  }
                  arrayX.reverse();
                  return arrayX.join("");
                },

                min: 0,
                maxTicksLimit: 5
              }
            }
          ],
          xAxes: [
            {
              gridLines: {
                zeroLineWidth: 0,
                display: false,
                drawBorder: false,
                drawOnChartArea: false,
                drawTicks: false
              },
              ticks: {
                display: true,
                fontColor: fontColor.branco,
                fontFamily: fontColor.font_principal,
                fontWidth: 100,
                fontStyle: "italic",
                beginAtZero: true,
                padding: 10
              }
            }
          ]
        }

        /*End options*/
      },
      datacollect: null
    };
  },
  methods: {
    fillData() {
      this.datacollect = {
        labels: [
          "Difícil Acesso",
          "Ausente",
          "Endereço Errado",
          "Desconhecido",
          "Mudou-se"
        ],
        datasets: [
          {
            label: "Population (millions)",
            backgroundColor: chartConfig.fontColor.azul_grafico,
            data: [49, 35, 30, 20, 1]
          }
        ]
      };
    }
  },
  mounted() {
    this.fillData();
  }
};
</script>

