<template>
  <div class="card card-chart-main d-flex p-0">
    <div class="card-header border-0">
      <div class="row">
        <h3
          class="col-6 col-sm-9 col-md-8 col-lg-10 text-left mt-1 name-chart-main"
        >Gráfico Geral {{handleData(5,0,currentState).stateDelivery}}</h3>
        <!-- <Select :ligado="ligado" @turn="ligado = !$event" class="col-3 col-sm-4 col-md-4 col-lg-4" /> -->
        <div class="d-flex col-6 col-sm-3 col-md-3 col-lg-2 view-chart card p-0 text-right">
          <h6 class="mr-1 mt-1">
            Postagem
            <i :class="greaterLastPeriod(handleData(5,0,currentState).greater)"></i>
            {{handleData(5,0,currentState).valueTotal}}
          </h6>
          <h6 class="mr-1">
            SLA
            <i :class="greaterLastPeriod(handleData(3,0,currentState).greater)"></i>
            {{handleData(3,0,currentState).percentage}}%
          </h6>
          <h6 class="mr-1">
            Devoluções
            <i :class="greaterLastPeriod(handleData(7,0,currentState).greater)"></i>
            {{handleData(7,0,currentState).percentage}}%
          </h6>
        </div>
      </div>
    </div>
    <div class="card-body text-primary border-0 d-flex p-0">
      <ChartLine
        :chart-data="datacollection"
        :options="optionChartMain"
        style="height:230px;position:relative;width:100%;"
      />
    </div>
    <div class="card-footer d-flex p-0 m-0">
      <div class="row p-0 m-0">
        <WhoDelivery class="col-md-6 col-lg-5 col-11" />
        <ChartTotalPost class="col-md-6 col-lg-7 col-11" />
      </div>
    </div>
    <ButtonFloat @trocar="setOpenTable" />
  </div>
</template>

<script>
import ChartTotalPost from "./../chart/TotalPost";
import WhoDelivery from "./../chart/WhoDelivery";
import ChartLine from "./../chart/ChartLine.js";
import * as chartConfig from "./../chart/ChartConfig";
//import Select from "./../element/Escolha";
import ButtonFloat from "./../element/buttonFloat";
export default {
  components: {
    //  Select,
    ButtonFloat,
    ChartLine,
    WhoDelivery,
    ChartTotalPost
  },
  computed: {
    currentState: {
      get() {
        return this.$store.state.currentState;
      },
      set(item) {
        this.$store.commit("setCurrentState", item);
      }
    }
  },
  data() {
    return {
      //   ligado: false,
      textDownValue: true,
      datacollection: null,
      optionChartMain: chartConfig.totalPedidos
    };
  },

  mounted() {
    this.fillData();
    setInterval(() => {
      this.currentState++;
      this.fillData();
    }, 10000);
  },
  methods: {
    handleData(item, month, uf) {
      var client = this.$store.state.client;
      var stateDelivery = this.$store.state.dataReceive[client].mainData.map(
        item => item[0]
      );
      function perUF(obj) {
        if (uf != "Geral") {
          if (stateDelivery[uf % 24] == obj[0]) {
            return true;
          } else {
            return false;
          }
        } else {
          return true;
        }
      }

      var valueTotal = this.$store.state.dataReceive[client][
        `mainData${month == 0 ? "" : "_" + month.toString()}`
      ]
        .filter(perUF)
        .map(Element => Element[item])
        .map(Element => parseFloat(Element))
        .reduce((soma, atual) => soma + atual)
        .toString();
      var valueTotal_1 = this.$store.state.dataReceive[client][
        `mainData${"_" + (month + 1).toString()}`
      ]
        .filter(perUF)
        .map(Element => Element[item])
        .map(Element => parseFloat(Element))
        .reduce((soma, atual) => soma + atual)
        .toString();

      return {
        stateDelivery: stateDelivery[uf % 24],
        greater: valueTotal > valueTotal_1,
        valueTotal: valueTotal,
        percentage: Math.abs((valueTotal / valueTotal_1) * 100 - 100)
          .toFixed(0)
          .toString()
      };
    },
    greaterLastPeriod(item) {
      return item ? "fas fa-chevron-up" : "fas fa-chevron-down";
    },
    setOpenTable() {
      this.$store.commit("setOpenTable", true);
    },

    fillData() {
      this.datacollection = {
        labels: ["Jan", "Fev", "Mar", "Abr", "Mai", "Jun"],
        datasets: [
          {
            label: "Postado",
            data: [
              this.handleData(5, 2, this.currentState).valueTotal * 1.5,
              this.handleData(5, 1, this.currentState).valueTotal * 1.1,
              this.handleData(5, 0, this.currentState).valueTotal,
              this.handleData(5, 2, this.currentState).valueTotal * 1.3,
              this.handleData(5, 1, this.currentState).valueTotal * 1.2,
              this.handleData(5, 0, this.currentState).valueTotal * 0.6
            ],
            borderWidth: 2.5,
            borderColor: chartConfig.fontColor.azul_grafico,
            //"rgb(230, 183, 51)",
            backgroundColor: chartConfig.fontColor.azul_grafico_opacity,
            fontColor: "rgb(230, 183, 51)",
            yAxisID: "left-y-axis",
            borderDash: [3, 2],
            pointHitRadius: 5,
            pointBackgroundColor: "rgba(0,0,0,0)"
          },
          {
            label: "SLA",
            data: [
              Math.round(
                this.handleData(3, 2, this.currentState).valueTotal * 100 * 100
              ) / 100,
              Math.round(
                this.handleData(3, 1, this.currentState).valueTotal * 100 * 100
              ) / 100,
              Math.round(
                this.handleData(3, 0, this.currentState).valueTotal * 100 * 103
              ) / 100,
              Math.round(
                this.handleData(3, 2, this.currentState).valueTotal * 100 * 120
              ) / 100,
              Math.round(
                this.handleData(3, 1, this.currentState).valueTotal * 100 * 130
              ) / 100,
              Math.round(
                this.handleData(3, 0, this.currentState).valueTotal * 100 * 110
              ) / 100
            ],
            borderWidth: 2.5,
            borderColor: chartConfig.fontColor.verde,
            backgroundColor: chartConfig.fontColor.verde_maisclaro_opacity,
            fontColor: "rgb(230, 183, 51)",
            yAxisID: "right-y-axis",
            pointHitRadius: 5,
            pointRadius: 5,
            pointBackgroundColor: chartConfig.fontColor.branco
          },
          {
            label: "Devolvido",
            pointRadius: 5,
            pointBackgroundColor: chartConfig.fontColor.branco,
            data: [
              Math.round(
                this.handleData(7, 2, this.currentState).valueTotal * 100 * 100
              ) / 100,
              Math.round(
                this.handleData(7, 1, this.currentState).valueTotal * 100 * 87
              ) / 100,
              Math.round(
                this.handleData(7, 0, this.currentState).valueTotal * 100 * 92
              ) / 100,
              Math.round(
                this.handleData(7, 2, this.currentState).valueTotal * 100 * 72
              ) / 100,
              Math.round(
                this.handleData(7, 1, this.currentState).valueTotal * 100 * 83
              ) / 100,
              Math.round(
                this.handleData(7, 0, this.currentState).valueTotal * 100 * 100
              ) / 100
            ],
            borderWidth: 2.5,
            borderColor: chartConfig.fontColor.rozado,
            backgroundColor: chartConfig.fontColor.vermelho_maisclaro_opacity,
            fontColor: "rgb(230, 183, 51)",
            yAxisID: "right-y-axis",
            pointHitRadius: 5,
            borderDash: [3, 10]
          }
        ]
      };
    }
  }
};
</script>

