<template>
  <ChartBar class="total-post" :options="optionTotalPost" :chart-data="datacollect" />
</template>
<script>
import ChartBar from "./ChartBar";
import * as chartConfig from "./ChartConfig";
export default {
  components: { ChartBar },
  computed: {
    currentState: {
      get() {
        return this.$store.state.currentState;
      },
      set(item) {
        this.$store.commit("setCurrentState", item);
      }
    }
  },
  data() {
    return {
      optionTotalPost: chartConfig.totalPost,
      datacollect: null
    };
  },
  methods: {
    handleData(item, month, uf) {
      var client = this.$store.state.client;
      var stateDelivery = this.$store.state.dataReceive[client].mainData.map(
        item => item[0]
      );
      function perUF(obj) {
        if (uf != "Geral") {
          if (stateDelivery[uf % 23] == obj[0]) {
            return true;
          } else {
            return false;
          }
        } else {
          return true;
        }
      }

      var valueTotal = this.$store.state.dataReceive[client][
        `mainData${month == 0 ? "" : "_" + month.toString()}`
      ]
        .filter(perUF)
        .map(Element => Element[item])
        .map(Element => parseFloat(Element))
        .reduce((soma, atual) => soma + atual)
        .toString();
      var valueTotal_1 = this.$store.state.dataReceive[client][
        `mainData${"_" + (month + 1).toString()}`
      ]
        .filter(perUF)
        .map(Element => Element[item])
        .map(Element => parseFloat(Element))
        .reduce((soma, atual) => soma + atual)
        .toString();

      return {
        stateDelivery: stateDelivery[uf % 24],
        greater: valueTotal > valueTotal_1,
        valueTotal: valueTotal,
        percentage: Math.abs((valueTotal / valueTotal_1) * 100 - 100)
          .toFixed(0)
          .toString()
      };
    },
    fillData() {
      this.datacollect = {
        labels: [
          this.handleData(5, 0, this.currentState).stateDelivery,
          this.handleData(5, 0, this.currentState - 1).stateDelivery,
          this.handleData(5, 0, this.currentState - 2).stateDelivery,
          this.handleData(5, 0, this.currentState - 3).stateDelivery,
          this.handleData(5, 0, this.currentState - 4).stateDelivery
        ],
        datasets: [
          {
            label: "Postagem",
            backgroundColor: chartConfig.fontColor.azul_grafico,
            data: [
              this.handleData(5, 0, this.currentState).valueTotal,
              this.handleData(5, 0, this.currentState - 1).valueTotal,
              this.handleData(5, 0, this.currentState - 2).valueTotal,
              this.handleData(5, 0, this.currentState - 3).valueTotal,
              this.handleData(5, 0, this.currentState - 4).valueTotal
            ]
          }
        ]
      };
    }
  },
  mounted() {
    this.fillData();
    setInterval(() => {
      this.fillData();
    }, 2000);
  }
};
</script>

