<template>
  <div class="card text-center" :class="'card-'+getTitleIcons(typeCard)[1]">
    <div class="card-header border-0">
      <span class="text-capitalize">
        <i :class="getTitleIcons(typeCard)[0]"></i>
        {{getTitleIcons(typeCard)[1]}}
      </span>
    </div>
    <div class="card-body text-primary border-0">
      <h2 class="card-value">{{value}}</h2>

      <h6 class="card-text" v-if="typeCard != 'postado'">
        <i :class="greaterLastPeriod(greater)"></i>
        {{textDownValue}}
      </h6>
    </div>
    <div class="card-footer p-0">
      <slot name="footer"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: ["typeCard", "greater", "value", "textDownValue"],
  data() {
    return {
      titleIcons: {
        postado: "fas fa-truck",
        recebido: "fas fa-box-open",
        devolvido: "fas fa-undo-alt",
        pendente: "fas fa-truck-loading"
      }
    };
  },
  methods: {
    getTitleIcons(item) {
      return [this.titleIcons[item], item];
    },
    greaterLastPeriod(item) {
      return item ? "fas fa-chevron-up" : "fas fa-chevron-down";
    }
  }
};
</script>

