export var totalPedidos = {
    responsive: true,
    maintainAspectRatio: false,
    /*layout:{
        padding:{
            left: 50
        }
    }*/
    tooltips: {
        callbacks: {
            label: function (tooltipItem, data) {
                var label = data.datasets[tooltipItem.datasetIndex].label || '';

                if (label == "Postado") {
                    label += ': ' + tooltipItem.yLabel;
                    return label
                } else {
                    label += ': '
                    label += tooltipItem.yLabel
                    return label + '%';
                }
            }
        }
    },
    legend: {
        display: true,
        labels: {
            fontColor: fontColor.branco,
            fontFamily: fontColor.font_principal,
            fontSize: 13

        }
    },
    scales: {
        yAxes: [{
            id: 'left-y-axis',
            gridLines: {
                zeroLineWidth: 0,
                zeroLineColor: "rgb(230, 183, 51)",
                display: true,
                color: fontColor.azul_maisclaro_opacity,
                drawOnChartArea: true
            },
            ticks: {
                fontColor: fontColor.branco,
                fontFamily: fontColor.font_principal,
                fontStyle: 'italic',
                fontSize: 15,
                padding: 30,
                callback: function (x) {
                    var arrayX = x.toString().split("")
                    arrayX.reverse()
                    let countLen = arrayX.length - 1
                    if (Math.floor(countLen / 3) > 0) {
                        for (let i = 0; i < Math.floor(countLen / 3); i++) {
                            arrayX.splice((3 * (i + 1)) + i, 0, ".")
                        }
                    }
                    arrayX.reverse()
                    return arrayX.join("")

                },

                min: 0,
                maxTicksLimit: 5,

            }
        }, {
            id: 'right-y-axis',
            position: 'right',
            gridLines: {
                zeroLineWidth: 0,
                zeroLineColor: "rgb(230, 183, 51)",
                display: false,
                color: fontColor.azul_maisclaro,
                drawOnChartArea: false
            },
            ticks: {
                fontColor: fontColor.branco,
                fontFamily: fontColor.font_principal,
                fontStyle: 'italic',
                fontSize: 15,
                padding: 30,
                callback: function (x) {

                    return x + "%"

                },
                max: 100,
                min: 0,
                maxTicksLimit: 6,

            }
        }





        ],
        xAxes: [
            {
                gridLines: {

                    zeroLineWidth: 0,
                    display: false,
                    drawBorder: false,
                    drawOnChartArea: false,
                    drawTicks: false

                },
                ticks: {
                    fontColor: fontColor.branco,
                    fontFamily: fontColor.font_principal,
                    fontWidth: 200,
                    fontStyle: 'italic',
                    beginAtZero: true,
                    padding: 15
                }
            }
        ]
    }
}



