<template>
  <div class="table-wrapper-scroll-y my-custom-scrollbar">
    <table class="table table-bordered table-striped mb-0">
      <thead>
        <tr>
          <th scope="col">UF</th>
          <th scope="col">Postagens</th>
          <th scope="col">SlA</th>
          <th scope="col">Devoluções</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in dataUf" :key="item[0]">
          <th scope="row">{{item[0]}}</th>
          <td class="text-center">{{item[5]}}</td>
          <td class="text-center">{{parseFloat(item[3]*100).toFixed(2)}}%</td>
          <td class="text-center">{{parseFloat(item[7]*100).toFixed(2)}}%</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  computed: {
    dataUf: {
      get() {
        return this.$store.state.dataReceive[this.$store.state.client][
          "mainData"
        ];
      }
    }
  }
};
</script>

<style>
.my-custom-scrollbar {
  position: relative;
  height: 350px;
  overflow: auto;
}
.table-wrapper-scroll-y {
  display: block;
}
</style>